body{
  background-color: rgb(255, 255, 255)
  
}
.card-img-top{
  height: 150px;
}
.card{
  height: 450px;
  border: 2px solid black;
  
}
.fixed-element {
  position: absolute;
  bottom: 0;
  left: 17%;
}
.readmore{
  text-decoration: none;
  border: 1px solid black;
  padding: 3px 6px;
  border-radius: 6px;
  font-weight: 500; 
  color: white;
  background-color: rgb(0, 0, 0);

}
.btn:hover{
  background-color: rgb(38, 37, 37);
  color: white;
}
li{
  list-style: none;
}
a{
  text-decoration: none;
}
.newscategorybox{
  background-color: rgb(0, 0, 0);
  padding: 8px;
  margin-top: 0px;
  
}
.newscategory{
  margin-left: 15px;
}
.newscategory:hover{
  font-weight: 600;
}
select{
  border-radius: 3px;
  display: block;
  float: right;
}
.footer{
  background-color: white;
  color: black;
  padding: 2px 7%;
}
.infinite-scroll-component{
  display: contents;
} 
.badge{
  left: 0;
  border-radius: 3px;
}
